@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

.common-countdown {
  .common-countdown-title {
    margin-bottom: 8px;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: $color-text-default;
    text-align: center;
    @include mobile-only {
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 10px;
    }
  }
  .common-countdown-list {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    .common-countdown-item {
      margin-right: 10px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .count-down-item {
    .count-down-item-body {
      position: relative;
      width: 42px;
      height: 40px;
      @include mobile-only {
        width: 36px;
        height: 34px;
      }
      .count-down-item-bg {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        .count-down-item-bg1 {
          margin-bottom: 2px;
          flex: 1;
          width: 100%;
          background: $color-input-bg-1;
          border-radius: 10px 10px 0px 0px;
          border: 1px solid $color-text-default-1;
          @include lightThemeOnly {
            background: rgba(229, 229, 229, 1);
          }
        }
        .count-down-item-bg2 {
          flex: 1;
          width: 100%;
          background: $color-input-bg-1;
          border-radius: 0px 0px 10px 10px;
          border: 1px solid $color-text-default-1;
          @include lightThemeOnly {
            background: rgba(229, 229, 229, 1);
          }
        }
      }
      .count-down-number {
        position: relative;
        z-index: 20;
        width: 100%;
        height: 100%;
        font-weight: 700;
        font-size: 30px;
        color: $color-text-default;
        line-height: 40px;
        text-align: center;
        margin-top: 5px;
        font-family: Helvetica;
        @include mobile-only {
          font-size: 24px;
          line-height: 34px;
        }
      }
    }
  }
  .count-down-desc {
    margin-top: 7px;
    text-align: center;
    font-weight: 400;
    font-size: 12px;
    color: $color-text-default-5;
    line-height: 14px;
    font-style: normal;
    font-family: PingFangSC-Regular;
    @include mobile-only {
      margin-top: 4px;
    }
  }
}
