@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

.details_info {
  display: flex;
  // align-items: flex-start;
  > div:nth-child(1) {
    flex: 1;
    // min-height: 690px;
  }
  > div:nth-child(2) {
    width: 400px;
  }
  &.details_info_connect {
    > div:nth-child(1) {
      flex: 1;
      // min-height: 884px;
    }
  }
  :global(.sun-empty-wrap) {
    padding-top: 40px;
    padding-bottom: 4px;
    margin-bottom: 0;
    min-height: 100px;
    img {
      margin-bottom: 14px;
      width: 64px;
      height: 64px;
    }
    p {
      font-family: PingFang SC;
      font-size: 16px;
      font-weight: 600;
      line-height: 22.4px;
      color: $color-text-default;
    }
    & > div {
      margin-top: 30px;
      width: 100%;
      height: 46px;
      line-height: 46px;
      border-radius: 46px;
      font-size: 16px;
      font-family: PingFang SC;
      font-weight: 600;
    }
  }
}
.details_info_m {
  :global(.sun-empty-wrap) {
    margin-bottom: 0;
    min-height: 220px;
    @include mobile-only {
      justify-content: end;
      min-height: 175px;
    }
    img {
      margin-bottom: 17px;
      width: 64px;
      height: 64px;
      @include mobile-only {
        width: 48px;
        height: 48px;
        margin-bottom: 15px;
      }
    }
    p {
      font-family: PingFang SC;
      font-size: 16px;
      font-weight: 600;
      line-height: 22.4px;
      color: $color-text-default;
      @include mobile-only {
        font-size: 14px;
        line-height: 20px;
      }
    }
    & > div {
      margin-top: 30px;
      width: 100%;
      height: 46px;
      line-height: 46px;
      border-radius: 46px;
      font-size: 16px;
      font-family: PingFang SC;
      font-weight: 600;
      @include mobile-only {
        margin-top: 16px;
      }
    }
  }
}
.detail_step {
  background: $color-normal-card-bg;
  border-radius: 10px;
  padding: 28px 30px;
  margin: 20px 0;
  @include mobile-only {
    margin: 10px 0;
    padding: 15px;
  }
}
.detail_title {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin: 40px 0 30px;
  @include mobile-only {
    display: block;
    margin: 20px 0 15px;
  }
  &_mobile {
    display: none;
    @include mobile-only {
      display: initial;
    }
  }

  &_flex {
    display: flex;
    &_l {
      margin-right: 8px;
      @include mobile-only {
        display: none;
      }
    }
    &_r {
      flex: 1;
      a {
        font-family: PingFangSC-Regular;
        font-weight: 300;
      }
    }
  }

  &_h1 {
    display: flex;
    flex: 1;
    margin-right: 10px;
    font-size: 32px;
    font-weight: 600;
    line-height: 45px;
    color: $color-text-default;
    // display: flex;
    // align-items: center;
    margin-bottom: 16px;
    @include mobile-only {
      font-size: 18px;
      line-height: 25px;
      margin-right: 0;
      margin-bottom: 6px;
    }
    &_text {
      flex: 1;
    }
    .detail_title_tag {
      margin-right: 10px;
      margin-left: 0;
      top: 4px;
    }
  }
  &_tag {
    position: relative;
    top: 12px;
    padding: 0 7px;
    border-radius: 2px;
    background: $color-text-default-1;
    color: $color-text-default;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    text-align: center;

    @include lightThemeOnly {
      background: #3333330a;
    }
    @include mobile-only {
      top: -2px;
      height: 17px;
      line-height: 17px;
      margin-left: 8px;
    }
  }
  &_desc {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    color: $color-text-default-6;
    margin-right: 30px;
    font-family: PingFangSC-Regular;
    @include mobile-only {
      display: block;
      margin-bottom: 10px;
    }
  }
  &_icon {
    color: $color-text-default;
    vertical-align: -1px;
    margin-left: 7px;
  }
}

.detail_card_title_m {
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  color: $color-text-default;
  margin-bottom: 10px;
}
