@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

.vote_container {
  display: flex;
  padding: 30px;
  background: $color-normal-card-bg;
  border-radius: 10px;
  @include mobile-only {
    padding: 10px;
  }
}

.vote_option {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  &:first-child {
    margin-right: 20px;
  }
  &:last-child {
    margin-left: 20px;
  }
  .vote_slider_label {
    font-family: PingFang SC;
    font-size: 26px;
    font-weight: 600;
    line-height: 36.4px;
    text-align: left;
    color: $color-text-default;
    margin-bottom: 10px;
  }
  .vote_option_img {
    width: 46px;
  }
}

.vote_slider_container {
  flex: 1;
  .vote_details {
    display: flex;
    justify-content: space-between;
    > span {
      font-family: PingFang SC;
      font-size: 16px;
      font-weight: 600;
      line-height: 22.4px;
      color: $color-text-default;
    }
    &_votes {
      @include mobile-only {
        display: none;
      }
      img {
        position: relative;
        top: -3px;
        width: 14px;
        margin-right: 10px;
      }
      &_text {
        font-family: PingFang SC;
        font-size: 14px;
        font-weight: 600;
        line-height: 19.6px;
        text-align: left;
      }
      &_divider {
        width: 1px;
        height: 12px;
        display: inline-block;
        @include darkThemeOnly {
          background-color: #e9e9e9;
          opacity: 0.2;
        }
        @include lightThemeOnly {
          background-color: #e5e5e5;
        }
      }
      &_for {
        color: #ff8700;
        margin-left: 16px;
        line-height: 12px;
        // padding-left: 16px;
      }
      &_against {
        color: #7378ff;
        margin-right: 16px;
        padding-right: 16px;
      }
    }
    margin-bottom: 8px;
  }
}
