@import '../../../assets/css/theme';

.back {
  color: $color-text-default;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  opacity: 0.6;
  cursor: pointer;
  display: inline-block;
  transition: transform 0.3s ease;
  font-family: PingFangSC-Regular;
  &_title {
    margin-left: 10px;
  }
  &:hover {
    opacity: 1;
    transform: translateX(-4px);
  }
}
