@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

.your_info_title {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px dashed $color-text-default-1;
  padding-bottom: 30px;
  color: $color-text-default;
  font-family: PingFang SC;
  font-size: 20px;
  font-weight: 600;
  line-height: 28px;
  text-align: left;
}

.your_info_content {
  padding: 37px 0 20px;
  border-bottom: 1px dashed $color-text-default-1;
  &_title {
    font-family: PingFang SC;
    font-size: 18px;
    font-weight: 600;
    line-height: 25.2px;
    text-align: left;
    color: $color-text-default;
  }
  &_tips {
    font-family: PingFang SC;
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;
    text-align: right;
    color: rgba(255, 172, 0, 1);
    margin-top: 10px;
    img {
      position: relative;
      top: -2px;
      margin-right: 6px;
    }
  }
  &_for_vote_tips {
    :global(.ant-tooltip-inner) {
      // word-break: break-all;
      width: 205px;
    }
  }
}

.your_info_single {
  display: flex;
  justify-content: space-between;
  align-items: center;
  > span:nth-child(1) {
    font-family: PingFang SC;
    font-size: 14px;
    font-weight: 600;
    line-height: 19.6px;
    text-align: left;
    color: $color-text-default-7;
  }
  > span:nth-child(2) {
    font-family: PingFang SC;
    font-size: 14px;
    font-weight: 600;
    line-height: 33.6px;
    text-align: right;
    display: flex;
    align-items: center;
    color: $color-text-default;
    img {
      display: block;
      position: relative;
      top: -2px;
    }
  }
  &_disabled {
    opacity: 0.3;
  }
}

.your_info_more {
  padding-top: 30px;
  text-align: center;
  > a {
    font-family: PingFang SC;
    font-size: 16px;
    font-weight: 600;
    line-height: 22.4px;
    color: $color-a;
  }
  > div {
    font-family: PingFang SC;
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;
    color: $color-text-default-4;
    margin-top: 10px;
  }
}
