@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

$icon-with: 24px;
$icon-with-m: 20px;

.step_bar {
  display: flex;
  align-items: center;
  &_icon {
    > img {
      width: $icon-with;
      height: $icon-with;
      @include mobile-only {
        width: $icon-with-m;
        height: $icon-with-m;
      }
    }
  }
  &_icon_wait,
  &_icon_process,
  &_icon_finish,
  &_icon_error {
    display: inline-block;
    width: $icon-with;
    height: $icon-with;
    border-radius: 50%;
    line-height: $icon-with;
    text-align: center;
    @include mobile-only {
      width: $icon-with-m;
      height: $icon-with-m;
      line-height: $icon-with-m;
    }
  }
  &_icon_wait {
    background: #2b243d;
    color: #c1a0ff;
    opacity: 0.6;
    @include lightThemeOnly {
      background: #eeeeee;
      color: #722fff;
    }
  }
  &_icon_process {
    background: #c1a0ff;
    color: #2b243d;
  }
  &_icon_finish {
    background: #1ba08a;
    color: #fff;
    border: 1px solid #51d2b8cc;
  }
  &_icon_error {
    border: 1px solid #ff9aac;
    background: #ff496a;
    color: #fff;
  }
  &_item {
    display: flex;
    align-items: center;
    &_wrap {
      display: flex;
      align-items: center;
    }
    &_content {
      & > div {
        margin-bottom: 4px;
      }
      & > div:last-child {
        margin-bottom: 0;
      }
    }
    &_title {
      font-size: 14px;
      font-weight: 500;
      line-height: 20px;
      color: $color-text-default;
    }
    &_desc {
      font-size: 12px;
      font-weight: 400;
      line-height: 17px;
      color: $color-text-default-6;
      font-family: PingFangSC-Regular;
    }
    &_wait &_title {
      color: $color-text-default-6;
    }
    &_title {
      font-family: PingFangSC-Medium;
    }
    //&_wait {
    //  opacity: 0.6;
    //}
  }
  &_icon {
    margin-right: 10px;
    @include mobile-only {
      margin-right: 15px;
    }
  }
  &_line {
    margin: 0 20px;
    background-image: url('../../../assets/images/step_line.png');
    background-repeat: no-repeat;
    width: 60px;
    height: 4px;
    @include lightThemeOnly {
      background-image: url('../../../assets/images/step_line_light.svg');
    }
  }
  &_vertical {
    display: block;
    &_tail {
      top: $icon-with;
      bottom: 6px;
      left: calc(($icon-with / 2) - 1px);
      position: absolute;
      width: 4px;
      background-image: url('../../../assets/images/step_dash_arrow_dark.svg');
      background-repeat: no-repeat;
      background-position: bottom center;
      background-size: 4px 8px;
      @include mobile-only {
        top: $icon-with-m;
        left: calc(($icon-with-m / 2) - 2px);
      }
      &::after {
        content: '';
        position: absolute;
        bottom: -6px;
        height: 6px;
        left: 1px;
        width: 2px;
        background-image: url('../../../assets/images/step_dash_dark.svg');
        background-repeat: repeat-y;
        background-position: center;
      }
      &::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 9px;
        left: 1px;
        width: 2px;
        background-image: url('../../../assets/images/step_dash_dark.svg');
        background-repeat: repeat-y;
        background-position: center;
      }
    }
  }
  &_vertical &_item {
    position: relative;
    padding-bottom: 20px;
  }
  &_vertical &_item_wrap {
    display: block;
    &:last-child {
      .step_bar_item {
        padding-bottom: 0;
      }
    }
  }
  &_vertical &_item_content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1;
  }
  &_vertical &_item_title {
    margin-bottom: 0;
  }
  &_vertical &_icon {
    position: relative;
    z-index: 2;
  }
}
