@import '../../assets/css/mixins';
@import '../../assets/css/theme';

.htx-vote-modal {
  .htx-vote-modal-title {
    padding: 0 0 30px 0;
    font-family: PingFangSC, PingFang SC;
    font-size: 20px;
    font-weight: 600;
    color: $color-text-default;
    line-height: 30px;
    text-align: center;
    font-style: normal;
  }
  .htx-vote-modal-icon {
    margin-bottom: 30px;
    img {
      display: block;
      margin: 0 auto;
      width: 90px;
    }
  }

  .htx-vote-own {
    color: $color-text-default-7;
    .htx-vote-sub-title {
      font-family: PingFangSC, PingFang SC;
      font-weight: 600;
      font-size: 16px;
      color: $color-text-default;
      line-height: 22px;
      font-style: normal;
    }
  }

  .htx-vote-own-desc {
    margin-top: 7px;
    font-family: PingFangSC, PingFang SC;
    font-weight: 400;
    font-size: 12px;
    color: $color-text-default;
    line-height: 17px;
    font-style: normal;
    .htx-vote-own-desc-label {
      margin-right: 6px;
      color: $color-text-default-7;
    }
  }
  .htx-vote-power-desc {
    margin-top: 10px;
    font-family: PingFangSC, PingFang SC;
    font-weight: 600;
    font-size: 24px;
    color: $color-text-default;
    line-height: 33px;
    font-style: normal;
    span {
      vertical-align: sub;
      &:first-child {
        margin-right: 8px;
      }
    }
    .htx-vote-power-value {
      font-size: 14px;
      color: $color-text-default-5;
    }
  }

  .htx-vote-submit {
    margin-top: 20px;
    button {
      margin: 0;
      padding: 20px 0;
      height: auto;
      width: 100%;
      line-height: 24px;
      font-size: 20px;
      background: $color-button-primary;
      box-shadow: 0px 0px 20px 0px rgba(16, 12, 29, 0.1);
      border-radius: 10px;
      &:active,
      &:disabled {
        opacity: 0.7;
      }
    }
  }

  .htx-vote-bottom-tips {
    margin-top: 16px;
    display: flex;
    align-items: flex-start;
    img {
      margin: 2px 10px 0 0;
    }
    span {
      font-family: PingFang SC;
      font-size: 14px;
      font-weight: 400;
      line-height: 19.6px;
      text-align: left;
      color: $color-text-default-6;
    }
  }

  .vote-select-list {
    display: flex;
    margin: 16px 0 20px;
    .vote-select-item {
      margin-right: 8px;
      flex: 1;
      padding: 7px 0;
      background: rgba(193, 160, 255, 0.1);
      border-radius: 5px;
      border: 1px solid rgba(255, 255, 255, 0.15);
      color: $color-button-link-text;
      font-size: 14px;
      text-align: center;
      user-select: none;
      cursor: pointer;
      transition: background-color 0.2s ease-in, color 0.2s ease-in;
      &:last-child {
        margin-right: 0;
      }
    }
    .vote-select-item-active {
      background: $color-button-primary;
      color: $white;
    }
  }
}
