@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

.vote_slider {
  display: flex;
  height: 32px;
  margin-bottom: 18px;
  margin-top: 15px;
  &_l {
    position: relative;
    min-width: 110px;
    background: linear-gradient(113.7deg, #ff7a00 21.03%, #ffd600 77.95%);
    border-radius: 10px 0 0 10px;
    border: 2px solid rgba($color: #ffffff, $alpha: 0.8);
    border-right: 0;
    transition: width 0.3s ease-in;
    background-origin: border-box;

    box-shadow: 0px 5px 15px 0px rgba(32, 20, 57, 1);
    @include lightThemeOnly {
      box-shadow: 0px 5px 15px 0px rgba(255, 187, 1, 0.3);
    }
  }
  &_vs {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(-50%, -50%);
    img {
      display: block;
      width: 44px;
    }
  }
  &_r {
    min-width: 120px;
    position: relative;
    flex: 1;
    background: linear-gradient(76.17deg, #50cbff 22.69%, #4148ff 79.4%);
    background-origin: border-box;
    border-radius: 0 10px 10px 0;
    border: 2px solid rgba($color: #ffffff, $alpha: 0.5);
    border-left: 0;
    transition: width 0.3s ease-in;
    box-shadow: 0px 5px 15px 0px rgba(32, 20, 57, 1);
    @include lightThemeOnly {
      box-shadow: 0px 5px 15px 0px rgba(66, 73, 255, 0.2);
    }
  }
}
.vote_slider_button {
  display: flex;
  justify-content: space-between;
  @include mobile-only {
    button {
      flex: 1;
      border-radius: 6px;
      margin-right: 25px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}
