@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

.proposals_info_title {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid $color-text-default-1;
  padding-bottom: 30px;
  > span {
    color: $color-text-default;
    font-family: PingFang SC;
    font-size: 20px;
    font-weight: 600;
    line-height: 28px;
    text-align: left;
  }
}

.proposals_info_content {
  font-family: PingFangSC-Medium;
  font-size: 14px;
  font-weight: 500;
  line-height: 32px;
  text-align: left;
  color: $color-text-default-7;
  padding-top: 20px;
}
